.form {
  flex-flow: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

label {
  text-align: center;
  margin: 5px;
  font-size: 22px;
  display: block;
}

input {
  width: 200px;
  height: 30px;
  text-align: center;
  border: 2px solid rgba(0, 80, 0, .5);
  border-radius: 5px;
  font-size: 20px;
  display: block;
}

input::-webkit-inner-spin-button {
  opacity: 1;
}

button {
  width: 200px;
  height: 35px;
  color: #fff;
  background: rgba(0, 80, 0, .5);
  border: none;
  border-radius: 5px;
  margin: 5px;
  font-size: 20px;
}

